.container-coverPage{
    height: 100vh;
    width: 100%;
    object-fit: contain;
    box-shadow: inset 0 0 0 1000px rgba(0, 0, 0, 0.783);
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    color: #fff;
}

.container-coverPage video{
    object-fit: cover;
    width: 100%;
    height: 100vh;
    position: absolute;
    z-index: -1;
}

.container-coverPage h1{
    font-size: 3rem;
    letter-spacing: 3px;
    text-transform: capitalize;
}

.showPerfil{
    line-height: 2.5;
    font-size: 1.7rem;
    color: #00fdf1;
    display: hidden;
    animation-duration: 3s;
    animation-name: slidein;
}

@keyframes slidein {
  from {
    margin-left: 10%;
    width: 70%;
    color: #00000000;
  }

  to {
    margin-left: 25.2%;
    width: 50%;
  }
}

.hidePerfil{
  display: none;
}

@media screen and (max-width: 600px) {
    .container-coverPage h1 {
      font-size: 30px;
      text-align: center;
    }
  
    .showPerfil {
      text-align: center;
      font-size: 22px;
      width: 85%;
      display: hidden;

    }

    .hidePerfil{
      display: none;
    }
  }